___CSS_LOADER_EXPORT___.push([module.id, `#cityCard {
  border: 1px solid #000;
  background-color: #ffffff;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  border-radius: 32px;
  padding: 32px;
  text-align: center;

  width: 500px;
  height: 300px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  & h2 {
    font-size: 2.5rem;
  }
}

.error {
  color: #e61c1c;
}

.temp {
  font-size: 3rem;
  color: #0093e9;
  cursor: pointer;
  transition: color 0.5s;

  &:hover {
    color: #fd1d1d;
  }
}

.loading {
  border: 4px solid rgba(0, 0, 0, 0.1);
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border-left-color: #09f;
  display: block;
  margin-inline: auto;

  animation: spin 1s ease infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
